import ApiService from "@/core/services/ApiService";
import axios from 'axios';
import {httpOptionsFile, httpOptions} from '../../global';

const apiServie = new ApiService();

export default class SongService{
  getData() {
    return axios.get(`${ApiService.vueInstance.axios.defaults.baseURL}/song`, httpOptions());
  }

  edit(id: any) {
    return axios.get(`${ApiService.vueInstance.axios.defaults.baseURL}/song/${id}`, httpOptions());
  }

  create(data: any) {
    return axios.post(`${ApiService.vueInstance.axios.defaults.baseURL}/song`, data, httpOptionsFile());
  }

  update(id: any, data: any) {
    return axios.patch(`${ApiService.vueInstance.axios.defaults.baseURL}/song/${id}`, data, httpOptionsFile());
  }

  destroy(id:any) {
    return axios.delete(`${ApiService.vueInstance.axios.defaults.baseURL}/song/${id}`, httpOptions());
  }

  changeStatus(id: any, data: any) {
    return axios.patch(`${ApiService.vueInstance.axios.defaults.baseURL}/song/${id}/status`, data, httpOptions());
  }
}
