
import { defineComponent, onMounted, ref } from "vue";
import { useRouter, useRoute } from "vue-router";
import SongService from "@/store/modules/song/service/song.service";
import ISong from "@/store/modules/song/interface/song.interface";
import Swal from "sweetalert2/dist/sweetalert2.js";
import draggable from 'vuedraggable'
import { createToaster } from "@meforma/vue-toaster";

export default defineComponent({
  name: "Songs",
  components: {
	  draggable
  },
  setup() {
    	const router = useRouter();
		const songService = new SongService();
		const songs = ref<Array<any>>([]);
		const show = ref<Boolean>(false);
		const toaster = createToaster({ position: "bottom-left" });

		const getData = () => songService.getData()
		.then((response) => {
			response.data.forEach((element: any) => {
				element.active = (element.active == 1)
			});
			songs.value = response.data
		})
		.catch((error) => {
			
		});

		const saveStatus = (song: any) => songService.changeStatus(song.id, song)
        .then((response) => {
            Swal.fire(
            'Confirmación!',
            'Información actualizada',
            'success'
            )
        })
        .catch((error) => {
            Swal.fire(
            'Oh no!',
            'No se guardaron los cambios',
            'error'
            )
        });

		const changeValue = (e: any, song: any) => {
			Swal.fire({
				title: 'Confirmación!',
				text: "¿Esta seguro que desea cambiar el estado?",
				icon: 'question',
				showCancelButton: true,
				confirmButtonColor: '#3085d6',
				cancelButtonColor: '#d33',
				confirmButtonText: 'Si, Guardar',
				cancelButtonText: 'No, Cancelar',
				reverseButtons: true
				}).then((result) => {
					if (result.isConfirmed) {
						saveStatus(song);
					}
					else{
						songs.value.forEach((element: any, index: any) => {
							if(element.id == song.id){
								songs.value[index].active = !song.active;
							}
						});
					}
			})
		}

		const deleteData = (songId) => {
			Swal.fire({
				title: 'Confirmación!',
				text: "¿Esta seguro que desea eliminar el registro seleccionado?",
				icon: 'question',
				showCancelButton: true,
				confirmButtonColor: '#3085d6',
				cancelButtonColor: '#d33',
				confirmButtonText: 'Si, Eliminar',
				cancelButtonText: 'No, Cancelar',
				reverseButtons: true
				}).then((result) => {
					if (result.isConfirmed) {
						songService.destroy(songId)
						.then((response) => {
							Swal.fire(
							'Confirmación!',
							'Información eliminada exitosamente',
							'success'
							).then(result => {
								getData()
							});
						})
						.catch((error) => {
							Swal.fire(
							'Oh no!',
							'No se completo la operación',
							'error'
							);
						});
					}
			})
			
		}

		onMounted(() => {
			getData()
		});

		return {
			songs,
			changeValue,
			deleteData
		};
  },
});
